import { Card, CardContent, CardMedia, Typography }from '@mui/material';
import image from '../../assets/diverse-logo.svg'
import { useState } from 'react';


export default function SupplierCard({isXSmallScreen, isSmallScreen, isMediumScreen}) {
  const [isHovered, setIsHovered] = useState(false);

  const handleMouseEnter = () => {
    setIsHovered(true);
  };

  const handleMouseLeave = () => {
    setIsHovered(false);
  };
  return (
    <Card
    elevation={0}
    style={{
        backgroundColor: "var(--lt-grey)",
        minHeight:'480px',
        display: 'flex',
      flexDirection: 'column',
      justifyContent: isHovered ? 'center' : 'space-between',
      padding: '10px',
      borderRadius: '10px'
    }}
    onMouseEnter={handleMouseEnter}
    onMouseLeave={handleMouseLeave}>
       {isHovered ? 
        <CardMedia>
        <img src={image}
        alt="Diversity Procurement Analytics"
        style={{
          width:isSmallScreen||isXSmallScreen ? '200px' : '70px',          
          display: 'flex',
          padding:isSmallScreen||isXSmallScreen ? '40px' : '10px',

        }}/>
        </CardMedia>
        : 
        <CardMedia>
        <img src={image}
        alt="Diversity Procurement Analytics"
        style={{
            display: 'flex',
            margin: 'auto',
            padding:isSmallScreen||isXSmallScreen ? '40px' : '0',
        }}/>
        </CardMedia>}
      <CardContent style={{ }}>
        <Typography component="div"
        style={{
          textAlign: 'left',
          fontFamily: 'Lato',
          fontSize: isSmallScreen||isMediumScreen ? 'var(--h4)' :'var(--h3)',
          fontWeight: '600'
        }}>
         Diversity Procurement Analytics
        </Typography>
        {isHovered ? 
      <Typography color="text.secondary"
      style={{
        textAlign: 'left',
        fontFamily: 'Lato',
        fontSize:isSmallScreen||isMediumScreen ? 'var(--p)': 'var(--h4)'
      }}>
        Elevate your diversity procurement efforts with TalkDEl's Diversity Procurement Analytics. Our intuitive dashboard visualizes key metrics, helping you monitor spend with diverse suppliers, track ROl, and assess progress towards supplier diversity goals. Generate insightful reports and leverage Al-enhanced insights to uncover trends in your diversity procurement data. It's more than analytics; it's a strategy for success.
        </Typography>
      :
        <Typography color="text.secondary"
      style={{
        textAlign: 'left',
        fontFamily: 'Lato',
        fontSize:isSmallScreen||isMediumScreen ? 'var(--p)': 'var(--h4)'
      }}>
         Track, Analyze, and Excel
        </Typography>
}
      </CardContent>
    </Card>
  );
};


