import { Card, CardContent, CardMedia, Typography }from '@mui/material';
import image from '../../assets/erp-logo.svg'
import logoSm from "../../assets/erp-logo-sm.svg"
import { useState } from 'react';


export default function SeamlessCard({isXSmallScreen, isSmallScreen, isMediumScreen}) {
  const [isHovered, setIsHovered] = useState(false);

  const handleMouseEnter = () => {
    setIsHovered(true);
  };

  const handleMouseLeave = () => {
    setIsHovered(false);
  };
  return (
    <Card
    elevation={0}
    style={{
        backgroundColor: "var(--lt-grey)",
        minHeight:'480px',
        display: 'flex',
      flexDirection: 'column',
      justifyContent: isHovered ? 'center' : 'space-between',
      padding: '10px',
      borderRadius: '10px'
    }}
    onMouseEnter={handleMouseEnter}
    onMouseLeave={handleMouseLeave}>
        {isHovered ? 
        <CardMedia>
        <img src={logoSm}
        alt="Diversity Procurement Analytics"
        style={{
          width:isSmallScreen||isXSmallScreen ? '200px' : '70px',          
          display: 'flex',
          padding:isSmallScreen||isXSmallScreen ? '40px' : '0',

        }}/>
        </CardMedia>
        : 
        <CardMedia>
        <img src={image}
        alt="Diversity Procurement Analytics"
        style={{
            display: 'flex',
            margin: 'auto',
            padding:isSmallScreen||isXSmallScreen ? '40px' : '10px',
        }}/>
        </CardMedia>}
      <CardContent>
      <Typography
      style={{
        textAlign: 'left',
        fontFamily: 'Lato',
        fontSize: isSmallScreen||isMediumScreen ? 'var(--h4)' :'var(--h3)',
        fontWeight: '600'
      }}> 
        Seamless ERP Integration
        </Typography>
        {isHovered ? 
      <Typography color="text.secondary"
      style={{
        textAlign: 'left',
        fontFamily: 'Lato',
        fontSize:isSmallScreen||isMediumScreen ? 'var(--p)': 'var(--h4)'
      }}>
Experience the future of streamlined supplier diversity with TalkDEl's Seamless ERP Integration. Our platform seamlessly integrates with major ERP systems, including Microsoft Dynamics, SAP ERP, and Oracle ERP. Say goodbye to manual data entry and hello to effortless supplier data management. Simplify your sourcing and procurement by importing diverse supplier data directly into TalkDEl's diversity supplier feature.
Get ready to embrace efficiency with TalkDEl's ERP Integration.        </Typography>
      :
        <Typography color="text.secondary" 
        style={{
          textAlign: 'left',
          fontFamily: 'Lato',
          fontSize:isSmallScreen||isMediumScreen ? 'var(--p)': 'var(--h4)'
        }}>
        The Power of Unified Supplier Management
        </Typography>
}
      </CardContent>
    </Card>
  );
};


