import NavBar from "../../components/NavBar/NavBar"
import DarkFooter from "../../components/Footer/DarkFooter"
import HeroPage from "../HeroPage/HeroPage"
import ValuesPage from "../ValuesPage/ValuesPage";
import TeamPage from "../TeamPage/TeamPage";
import { useLocation } from 'react-router-dom';


export default function AboutUsPage({isXSmallScreen, isSmallScreen, isMediumScreen}) {
    const location = useLocation();
    const isAboutUsPage = location.pathname === '/aboutus';
    
    return (
    <>
    <NavBar isSmallScreen={isSmallScreen} isMediumScreen={isMediumScreen}isAboutUsState={isAboutUsPage} />
    <HeroPage isAboutUsState={isAboutUsPage} isMediumScreen={isMediumScreen} isSmallScreen={isSmallScreen}/>
    <ValuesPage isMediumScreen={isMediumScreen} isSmallScreen={isSmallScreen}/>
    <TeamPage isXSmallScreen={isXSmallScreen} isMediumScreen={isMediumScreen} isSmallScreen={isSmallScreen}/>
    <DarkFooter isSmallScreen={isSmallScreen} isMediumScreen={isMediumScreen}/>
    </>
    )
        
}