import { Card, CardContent, CardMedia, Typography }from '@mui/material';
import frameOne from '../../assets/frame-1.png'
import frameTwo from '../../assets/frame-2.png'
import frameThree from '../../assets/frame-3.png'
import smallAvatar from "../../assets/avatar-0.svg"
import { useState } from 'react';


export default function SupplierCard({isXSmallScreen, isSmallScreen, isMediumScreen}) {
  const [isHovered, setIsHovered] = useState(false);

  const handleMouseEnter = () => {
    setIsHovered(true);
  };

  const handleMouseLeave = () => {
    setIsHovered(false);
  };
  return (
    <Card
    elevation={0}
    style={{
      backgroundColor: "var(--lt-grey)",
      minHeight: '480px',
      display: 'flex',
      flexDirection: 'column',
      justifyContent: isHovered ? 'center' : 'space-between',
      padding: '10px',
      borderRadius: '10px'
    }}
    onMouseEnter={handleMouseEnter}
    onMouseLeave={handleMouseLeave}>
       {isHovered ? 
        <CardMedia>
        <img src={smallAvatar}
        alt="Diversity Procurement Analytics"
        style={{
          width:isSmallScreen||isXSmallScreen ? '200px' : '70px',          
          display: 'flex',
          padding:isSmallScreen||isXSmallScreen ? '40px' : '10px',

        }}/>
        </CardMedia>
        : 
        <CardMedia style={{width: '100%', margin: 'auto'}}>
        <img src={frameOne}
        alt="frame with user"
      style={{
        height: isSmallScreen||isXSmallScreen ? '50px' :'80px',
        margin: 'auto',
        marginTop: '20px',
        marginBottom: '20px'
        
      }}/>
      <img src={frameTwo}
      alt="frame with user"
       style={{
        height: isSmallScreen||isXSmallScreen ? '50px' :'80px',
        margin: 'auto',
        marginBottom: '20px'

      }}/>
      <img src={frameThree}
      alt="frame with user"
       style={{
        height: isSmallScreen||isXSmallScreen ? '50px' :'80px',
        margin: 'auto',
        marginBottom: '20px'

      }}/>
        </CardMedia>
}
        <CardContent>
        <Typography component="div"
        style={{
          textAlign: 'left',
          fontFamily: 'Lato',
          fontSize: isSmallScreen||isMediumScreen ? 'var(--h4)' :'var(--h3)',
          fontWeight: '600'
        }}>
         Supplier Discovery and Matching
        </Typography>
        {isHovered ? 
      <Typography color="text.secondary"
      style={{
        textAlign: 'left',
        fontFamily: 'Lato',
        fontSize:isSmallScreen||isMediumScreen ? 'var(--p)': 'var(--h4)'
      }}>
Unveil the future of supplier diversity with TalkDEl's Supplier Discovery and Matching feature. Our user-friendly platform empowers procurement professionals and diversity supply managers to efficiently discover and match with diverse suppliers. Harness the power of Al-driven recommendations, detailed supplier profiles, and advanced search functionality to connect with suppliers based on location, industry, diversity certifications, and more.<br></br>
Discover your ideal supplier match with TalkDEl.        </Typography>
      :
        <Typography color="text.secondary"
      style={{
        textAlign: 'left',
        fontFamily: 'Lato',
        fontSize:isSmallScreen||isMediumScreen ? 'var(--p)': 'var(--h4)'
      }}>
         Find the perfect fit
        </Typography>
}
        </CardContent>        
    </Card>
  );
};


