import "./App.css";
import {useMediaQuery} from '@mui/material';
import React from "react";
import { Route, Routes } from "react-router-dom";
import SignUp from "./pages/SignInPage/SignInPage";
import Home from "./pages/HomePage/HomePage";
import AboutUs from "./pages/AboutUsPage/AboutUsPage"

function App() {
  const isXSmallScreen = useMediaQuery('(min-width: 320px) and (max-width: 599px)');
  const isSmallScreen = useMediaQuery('(max-width: 600px)');
  const isMediumScreen = useMediaQuery('(min-width: 601px) and (max-width: 960px)');
  const isLargeScreen = useMediaQuery('(min-width: 961px)');
  return (
    <div className="App">
     <Routes>
      <Route path="/signup" element={<SignUp isSmallScreen={isSmallScreen} isMediumScreen={isMediumScreen} />} />
      <Route path="/" element={<Home isXSmallScreen={isXSmallScreen} isSmallScreen={isSmallScreen} isMediumScreen={isMediumScreen} isLargeScreen={isLargeScreen}/>} />
      <Route path="/aboutus" element={<AboutUs isXSmallScreen={isXSmallScreen} isSmallScreen={isSmallScreen} isMediumScreen={isMediumScreen} isLargeScreen={isLargeScreen}/>} />
    </Routes>
    </div>
  );
}

export default App;
