import { Avatar, Typography, Box } from "@mui/material";
import avatarOne from "../../assets/avatar-0.svg";
import avatarTwo from "../../assets/avatar-1.svg";
import avatarThree from "../../assets/avatar-2.svg";
import avatarFour from "../../assets/avatar-3.svg";
import avatarFive from "../../assets/avatar-4.svg";
import avatarSix from "../../assets/avatar-5.svg";

const avatars = [avatarOne, avatarTwo, avatarThree, avatarFour, avatarFive, avatarSix];

const RingComponent = ({ size, lineLength, lineWidth, imageRadius }) => {
  const ringStyle = {
    width: size,
    height: size,
    borderRadius: "50%",
    backgroundColor: "var(--lt-grey)",
    border: 'none',
    position: "relative",
    transform: "rotate(-30deg)",
  };

  const lineStyle = (rotation) => ({
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: `translate(-50%, -50%) rotate(${rotation}deg)`,
    width: lineWidth,
    height: lineLength,
    backgroundColor: "var(--tan)",
  });

  const imageStyle = (rotation, lineLength, imageRadius) => {
    const avatarX = 50 + Math.sin((rotation * Math.PI) / 180) * imageRadius;
    const avatarY = 50 - Math.cos((rotation * Math.PI) / 180) * imageRadius;

    return {
      width: "110px",
      height: "110px",
      borderRadius: "50%",
      position: "absolute",
      top: `${avatarY}%`,
      left: `${avatarX}%`,
      transform: `translate(-50%, -50%) rotate(30deg)`,
    };
  };

  return (
    <Typography elevation={3} sx={ringStyle}>
      {/* Small Teal Circle */}
      <div
        style={{
          position: "absolute",
          top: "50%",
          left: "50%",
          transform: "translate(-50%, -50%)",
          width: "140px",
          height: "140px",
          borderRadius: "50%",
          backgroundColor: "var(--teal)",
          zIndex: '100',
        }}
      />
      <div
        style={{
          position: "absolute",
          top: "50%",
          left: "50%",
          transform: "translate(-50%, -50%)",
          width: "120px",
          height: "120px",
          borderRadius: "50%",
          backgroundColor: "var(--lt-grey)",
          zIndex: '101',
        }}
      />

      {/* Six Lines */}
      {[0, 1, 2, 3, 4, 5].map((index) => (
        <div key={index} style={lineStyle(index * 60)} />
      ))}

      {/* Six Images Connected to Lines */}
      {avatars.map((avatar, index) => (
        <Avatar
          key={index}
          src={avatar}
          alt={`Image ${index}`}
          style={imageStyle(index * 60, lineLength, imageRadius)}
        />
      ))}
    </Typography>
  );
};

export default function RotatingAvatarCircle() {
  return (
    <Box style={{ display: "flex", justifyContent: "center", alignItems: "center", margin: '100px 70px' }}>
      <RingComponent size="380px" lineLength="360px" lineWidth="10px" imageRadius="60" />
    </Box>
  );
}
